$red: #ff0000;
 
html {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 0.875em;
}

body {
    margin: 0 auto;
    padding: 0 2em;
    display: flex;
    flex-direction: column;

}

h1 {
    color: $red;
    text-align: left;
}

.dices{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    width: 60%;
}

